import React from 'react';
import { Route } from 'react-router-dom';
import { IonApp, IonRouterOutlet, IonSplitPane } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { AppPage } from './declarations';

import Menu from './components/Menu';
import Home from './pages/Home';
import FreedomDividend from './pages/FreedomDividend';
import MedicareForAll from './pages/MedicareForAll';
import DemocracyDollars from './pages/DemocracyDollars';
import AllPolicies from './pages/AllPolicies';
import YangGang from './pages/YangGang';
import { home, list } from 'ionicons/icons';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

const appPages: AppPage[] = [
  {
    title: 'Home',
    url: '/',
    icon: home
  },
  {
    title: 'Freedom Dividend',
    url: '/freedom-dividend',
    icon: list
  },
  {
    title: 'Medicare For All',
    url: '/medicare-for-all',
    icon: list
  },
  {
    title: 'Democracy Dollars',
    url: '/democracy-dollars',
    icon: list
  },
  {
    title: 'All Policies',
    url: '/all-policies',
    icon: list
  },
  {
    title: '#YangGang',
    url: '/yang-gang',
    icon: list
  }
];

const App: React.FC = () => (
  <IonApp>
    <IonReactRouter>
      <IonSplitPane contentId="main">
        <Menu appPages={appPages} />
        <IonRouterOutlet id="main">
          <Route path="/" component={Home} exact={true} />
          <Route path="/freedom-dividend" component={FreedomDividend} exact={true} />
          <Route path="/medicare-for-all" component={MedicareForAll} exact={true} />
          <Route path="/democracy-dollars" component={DemocracyDollars} exact={true} />
          <Route path="/all-policies" component={AllPolicies} exact={true} />
          <Route path="/yang-gang" component={YangGang} exact={true} />
        </IonRouterOutlet>
      </IonSplitPane>
    </IonReactRouter>
  </IonApp>
);

export default App;
